@keyframes fade-in {
  0% {
    display: none;
    opacity: 0; }
  1% {
    display: block;
    opacity: 0; }
  100% {
    display: block;
    opacity: 1; } }

@keyframes fade-out {
  100% {
    display: block;
    opacity: 1; }
  1% {
    display: block;
    opacity: 0; }
  0% {
    display: none;
    opacity: 0; } }

.clearfix {
  overflow: hidden;
  zoom: 1;
  clear: both; }

body {
  background-image: url(../img/common/bg.jpg);
  overflow-x: hidden; }

main {
  display: block; }

h1 {
  margin: 40px auto 30px;
  font-size: 3.0rem;
  font-weight: bold; }

h2 {
  margin: 90px 0 40px;
  padding: 20px 40px;
  font-size: 2.0rem;
  font-weight: bold;
  line-height: 1.6;
  color: #e11f6a;
  text-align: center;
  background: url(../img/postdata_h2_bg.jpg);
  box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.5); }

h3 {
  font-size: 2.0rem;
  line-height: 1.8;
  margin: 80px 40px 30px;
  padding-bottom: 10px;
  font-weight: bold;
  text-align: center;
  color: #e11f6a;
  border-bottom: 1px solid #e11f6a; }
  @media screen and (max-width: 736px) {
    h3 {
      margin: 40px 20px 30px; } }

h4, h5, h6 {
  font-size: 1.8rem;
  line-height: 1.8;
  margin: 80px 40px 30px;
  font-weight: bold;
  text-align: center;
  color: #e11f6a; }
  @media screen and (max-width: 736px) {
    h4, h5, h6 {
      margin: 40px 20px 30px; } }

div {
  margin: 0 0 40px 0; }

p {
  font-size: 1.6rem;
  line-height: 1.6;
  margin: 0 40px 30px; }
  @media screen and (max-width: 736px) {
    p {
      margin: 0 20px 30px; } }

blockquote {
  margin: 0 10px 50px 0;
  padding: 40px 20px 20px 20px;
  border-left: 5px solid #b9b9b9;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #666;
  font-style: italic;
  box-sizing: border-box;
  position: relative; }
  @media screen and (max-width: 736px) {
    blockquote {
      margin: 0 10px 25px; } }
  blockquote::before, blockquote::after {
    font-size: 6.0rem;
    opacity: 0.5;
    color: #898989;
    position: absolute;
    clear: both;
    display: block; }
  blockquote::before {
    content: '“';
    top: -5px; }
  blockquote::after {
    content: '”';
    right: 10px;
    bottom: -30px; }
  blockquote p {
    word-wrap: break-word; }

table {
  margin: 0 40px 40px;
  background: url(../img/table_bg.jpg);
  box-sizing: border-box;
  font-size: 1.6rem;
  box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.5); }
  @media screen and (max-width: 736px) {
    table {
      margin: 0 20px 20px; } }
  table tr th, table tr td {
    padding: 14px;
    border-right: 1px solid #c8c8c8;
    vertical-align: middle;
    text-align: left;
    background: url(../img/widget_h_line.png) repeat-x; }
    table tr th:last-child, table tr td:last-child {
      border: none; }
  table tr th {
    font-weight: bold;
    background-color: rgba(207, 207, 207, 0.5); }
  table tr td::before, table tr td::after {
    content: "";
    height: 15px;
    border: none;
    display: table-cell; }
  @media screen and (max-width: 736px) {
    table {
      width: 95%;
      margin: 0 auto 40px;
      box-sizing: border-box;
      display: block;
      clear: both; }
      table thead {
        display: block;
        float: left;
        overflow-x: scroll; }
      table tbody {
        display: block;
        width: auto;
        overflow-x: auto;
        white-space: nowrap; }
      table tr {
        display: inline-block;
        margin: 0 -3px; }
        table tr th, table tr td {
          padding: 20px; }
          table tr th:last-child, table tr td:last-child {
            border-right: 1px solid #c8c8c8; }
        table tr th {
          box-sizing: border-box;
          display: block; }
        table tr td {
          display: block; }
          table tr td:first-child {
            text-align: left; }
          table tr td::before, table tr td::after {
            height: auto; } }

img {
  max-width: 100%;
  height: 100%; }
  @media screen and (max-width: 916px) {
    img {
      max-width: 320px; } }

ul, ol {
  margin: 0 40px 15px 60px;
  padding: 0 0 15px 20px;
  font-size: 1.4rem;
  line-height: 1.6; }
  ul:last-child, ol:last-child {
    padding-bottom: 0; }
  ul li, ol li {
    padding-top: 5px; }
  @media screen and (max-width: 1374px) {
    ul, ol {
      transition: 0.4s; } }
  @media screen and (max-width: 916px) {
    ul, ol {
      transition: 0.4s; } }
  @media screen and (max-width: 736px) {
    ul, ol {
      margin: 0 20px 20px 20px;
      transition: 0.4s; } }

ul {
  list-style-type: disc; }

ol {
  list-style-type: decimal;
  line-height: 1.8; }

dl {
  margin: 0 40px 40px;
  font-size: 1.4rem;
  line-height: 1.4; }
  dl dt {
    font-weight: bold;
    margin-bottom: 10px; }
  dl dd {
    margin-left: 20px; }
  @media screen and (max-width: 736px) {
    dl {
      margin: 0 20px 20px; } }

pre {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  white-space: pre-wrap;
  word-wrap: break-word;
  background: url(../img/widget_h_line.png) repeat-x bottom #fff;
  margin-bottom: 20px;
  line-height: 1.4; }

code {
  background: #d6d6d6;
  padding: 3px 5px;
  font-weight: bold; }

strong {
  font-weight: bold; }

sup {
  vertical-align: super;
  font-size: smaller; }

sub {
  vertical-align: sub;
  font-size: smaller; }

address {
  padding-left: 40px; }

iframe {
  max-width: 100%; }

.imgauto {
  width: auto;
  height: auto; }

html {
  font-size: 62.5%; }

body {
  font-family: 'ヒラギノ明朝 Pro W3', 'Hiragino Mincho Pro', "ＭＳ Ｐ明朝","MS PMincho", 'メイリオ', Meiryo, sans-serif;
  color: #595959; }

.required {
  color: #f00; }

body {
  background-color: #eaeae8 !important;
  background-image: none !important; }
